.comparison {
  &__data {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    column-gap: 10px;
    margin-top: 20px;
    margin-bottom: 20px;

    @media (min-width: 768px) {
        flex-direction: row;
        align-items: flex-start;
    }

    @media (min-width: 1201px) {
      margin-bottom: 50px;
    }
  }

  &__controllers {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;

    @media (min-width: 768px) {
        flex: 1 0 auto;
        max-width: 280px;
    }

    @media (min-width: 992px) {
        max-width: 320px;
    }

    .btn {
        font-family: "GothamPro-Medium", sans-serif;
        height: 40px;
        font-size: 12px;
        box-shadow: none;
    }

    @media (max-width: 1199px) {
      .btn_black {
        &:hover {
          color: #fff;
          background: #000;
        }
      }

      .btn_white {
        color: #000;
        background: #fff;
      }
    }
  }

  &__products {
    width: 100%;
    overflow: hidden;

    .swiper {
      width: 100%;
      padding-bottom: 59px;

      @media (min-width: 1200px) {
          padding-bottom: 67px;
      }
    }

    .swiper-slide {
      height: auto;
    }

    .swiper-scrollbar {
      bottom: 0 !important;
      height: 40px !important;
      width: calc(100% - 120px) !important;
      left: 60px !important;
      background-color: transparent !important;

      &::before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #eeeeee;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 999px;
      }
    }

    .swiper-scrollbar-drag {
      background-color: transparent !important;
      cursor: pointer;

      &::before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #000;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 999px;
      }
    }

    .swiper-button-prev, 
    .swiper-button-next {
      width: 40px;
      height: 40px;
      background: #eee;
      border-radius: 50%;
      top: initial;
      bottom: 0;
    }

    .swiper-button-prev {
      left: 0;
    }

    .swiper-button-next {
      right: 0;
    }

    .swiper-button-prev:after,
    .swiper-button-next:after {
      color: #000;
      font-size: 10px;
      font-weight: bold;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    padding: 10px;
    color: black !important;
    text-decoration: none !important;

    @media (min-width: 1200px) {
      padding-bottom: 14px;
      box-shadow: inset 0 0 0 0 transparent;
      transition: all .1s linear;

        &:hover {
          box-shadow: inset 0 0 0 1px black;
        }
    }

    div {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-start;
      height: 100%;
    }

    &-del {
      display: block;
      position: absolute;
      z-index: 1;
      top: 10px;
      right: 10px;
      width: 20px;
      height: 20px;
      border: none;
      background-color: transparent;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml;base64,");
      transition: all .1s;

      @media (min-width: 1200px) {
          top: 20px;
          right: 20px;

          &:hover {
            background-color: black;
            background-image: url("data:image/svg+xml;base64,");
          }
      }
    }

    &-pic {
      display: block;
      position: relative;
      width: 100%;
      max-width: 305px;
      height: auto;
      margin-bottom: 10px;
      background-color: transparent;

      @media (min-width: 1200px) {
          margin-bottom: 20px;
      }

      &::before {
        content: '';
        display: block;
        position: relative;
        width: 100%;
        height: auto;
        padding-bottom: 100%;
      }

      img {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;

        &::before {
          content: '';
          display: block;
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 1;
          background-color: #f6f6f6;
          background-position: center;
          background-repeat: no-repeat;
          background-size: 120%;
          background-image: url("data:image/svg+xml;base64,");
        }
      }
    }

    &-title {
      flex: 1 0 auto;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      font-family: "GothamPro-Medium", sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 18px;
      margin: 7px 0 1px;
      padding: 0;
      color: #000 !important;
      text-decoration: none !important;

      @media (min-width: 1200px) {
        margin: 5px 0 6px;
      }
    }

    &-code {

      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      position: relative;
      margin: 8px 0;
      padding: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: #000 !important;
      text-decoration: underline;
      text-decoration-color: rgba(0,0,0,.3) !important;
    }

    &-price {
      margin: 5px 0;
      padding: 0;
      font-family: "GothamPro-Bold", sans-serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 111.69%;
      color: #000 !important;
      text-decoration: none !important;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 40px;
    width: 100%;
    height: auto;
    overflow-x: auto;
    padding-bottom: 15px;
    margin-bottom: 40px;

    @media (min-width: 1201px) {
      gap: 35px;
    }
  }

  &__with-icon {
    display: inline-flex;
    align-items: center;
    column-gap: 8px;
    min-height: unset !important;
  }

  &__table {
    display: flex;
    flex-wrap: wrap;
    width: auto;
    height: auto;
    border-spacing: 0px;
    border-collapse: collapse;
    font-weight: 400;
    line-height: 1.3;
    font-size: 15px;
    color: #000;

    @media (min-width: 1201px) {
        font-size: 17px;
    }

    tbody {
      width: auto;
      display: block;
      box-shadow: inset 0px 1px 0px 1px rgba(0,0,0,1);

      @media (min-width: 1201px) {
          border-bottom: 1px solid #000;
      }
    }

    tr {
      display: flex;
      align-items: stretch;
      position: relative;
      width: 100%;
      height: auto;

      &::before {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
      }

      @media (min-width: 1201px) {
        &:nth-child(4n + 1) {
          &::before {
            content: '';
            background-color: #f6f6f6;
          }            
        }

        &.painted::before {
          content: '';
          background-color: #f6f6f6; 
        }
        &.not-painted::before {
          content: '';
          background-color: transparent; 
        }
      }
    }

    th, td {
      padding-left: 10px;
      padding-top: 9px;
      padding-bottom: 9px;
      min-height: 40px;

      @media (min-width: 1201px) {
        padding-top: 9px;
        padding-bottom: 8px;
      }
    }

    th {
      display: flex;
      align-items: center;
      width: 100%;
      border-bottom: 1px solid #000;
    }

    td {
      display: flex;
      flex-wrap: wrap;
      border-bottom: 1px solid #000;

      @media (min-width: 1201px) {
        border-bottom: none;
      }
    }

    a {
      display: inline-flex;
      align-items: center;
      color: #2399e2;
    }

    &-caption {
      background-color: black !important;
      
      th {             
        padding-top: 8px;
        padding-bottom: 8px;
        font-family: "GothamPro-Medium", sans-serif;        
        font-size: 17px;
        color: #fff;
      }
    }

    &-title {

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 1px;
        width: calc(100% - 2px);
        height: 100%;
        background-color: #f6f6f6;
        z-index: -1;
      }

      @media (min-width: 1201px) {
        display: none !important;
      }
    }

    &-sign {
      width: 330px !important;

      @media (max-width: 1200px) {
        display: none !important;
      }
    }

    &-row {
      td {
        width: calc((100vw - 40px) / 2);
        border-right: 1px solid #000;

        @media (min-width: 576px) {
            width: calc((100vw - 40px) / 3);
        }

        @media (min-width: 768px) {
            width: calc((100vw - 40px) / 4);
        }

        @media (min-width: 981px) {
            width: calc((950px - 40px) / 4);
        }

        @media (min-width: 1201px) {
          width: 280px;
        }

        ul {
          display: flex;
          flex-direction: column;
          list-style: none;
          margin: 0;
          padding: 0;
        }

        li {
          padding: 12.5px 0;

          &:first-child { padding-top: 2px; }

          &:last-child { 
            padding-bottom: 4px; 

            @media (min-width: 1201px) {
               padding-bottom: 3px;
            }
          }

          @media (min-width: 1201px) {
              padding: 11.5px 0;
          }
        }
      }
    }
  }

  &__tooltip {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml;base64,");
  }

  .tooltip {
    opacity: 1;
  }

  .tooltip-inner {
    font-family: "GothamPro-Medium", sans-serif;
    text-align: left;
    border-radius: 0;
    text-align: left;
    padding: 15px;
    font-size: 16px;
    line-height: 1.2;
    width: 200px;
    max-width: 200px;

    @media (min-width: 1200px) {
        width: 250px;
        max-width: 250px;
    }
  }

  .tooltip.right {
    margin-left: 12px;

    .tooltip-arrow {
      margin-top: -14px;
      margin-left: -8px;
      border-width: 13px 13px 13px 0;
    }
  }

  .custom-scroll-horizontal {
    //- Стили для firefox
    scrollbar-color: #000 transparent;
    scrollbar-width: thin;

    //- скроллбар
    &::-webkit-scrollbar {
      width: 0;
      height: 5px;
      background: #fff;
    }

    //- кнопки управления полузунком и скроллом
    &::-webkit-scrollbar-button {
      display: none;
    }

    //- Цвет дорожки, по которой двигается бегунок прокрутки.
    &::-webkit-scrollbar-track {
      // background: linear-gradient(to bottom,
      //   #fff 2px,
      //   #000 2px,
      //   #000 3px,
      //   #fff 3px,
      //   #fff 100%);
      background-color: #eee;
      border-radius: 999px;
    }

    //- видимая часть трека
    &::-webkit-scrollbar-track-piece {
    }

    //- Цвет бегунка полосы, а так же его закругление.
    &::-webkit-scrollbar-thumb {
      background-color: #000;
      border-radius: 999px;
    }

    //- уголоки на кнопках уаправления ползунком
    &::-webkit-scrollbar-corner {
      display: none;
    }

    //- Основная ширина полосы прокрутки.
    &::-webkit-resizer {
    }
  }

  &__modal {
    overflow-x: hidden;
    overflow-y: auto;

    &-dialog {
      width: 290px !important;
      margin-top: 40px !important;
      margin-bottom: 40px !important;

      @media (min-width: 1200px) {
          width: 1083px !important;
          margin-top: 100px !important;
      }
    }

    &-content {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 290px;
      background: #fff;
      border: 1px solid #000;
      padding: 68px 20px 20px;

      @media (min-width: 1200px) {
        flex-direction: row;
        gap: 21px;
        width: 1083px;
        padding: 19px;
      }
    }

    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: absolute;
      top: 18px;
      left: 0;
      width: 100%;
      height: 30px;
      padding: 0 13px 0 19px;

      @media (min-width: 1200px) {
        position: relative;
        top: 0;
        padding: 0;
        height: 40px;
        margin-bottom: 27px;
      }

      i {
        flex: 1 0 auto;
        display: block;
        width: 30px;
        max-width: 30px;
        height: 30px;
        background-color: #000;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml;base64,");
      
        @media (min-width: 1200px) {
          height: 40px;
          width: 40px;
          min-width: 40px;
        }
      }

      p {
          font-family: "GothamPro-Bold", sans-serif !important;
          font-style: normal !important;
          font-weight: 700 !important;
          font-size: 14px !important;
          line-height: 110%;
          text-transform: uppercase;
          padding: 0 10px;
          text-align: left;
          margin: 0 !important;

          @media (min-width: 1200px) {
            width: 100%;
            padding: 0 20px 2px 20px;
            font-size: 15px!important;
            min-height: initial !important;
            margin-bottom: 0 !important;
          }
      }

      button {
        flex: 1 0 auto;
        display: block;
        position: relative;
        margin-left: 25px;
        width: 30px;
        max-width: 30px;
        height: 30px;
        border: none;
        margin-top: 1px;
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml;base64,");
        transition: all .1s linear;

        @media (min-width: 1200px) {
          width: 40px;
          min-width: 40px;
          height: 40px;
          margin-top: 0px;
        }

        &:hover {
          opacity: 0.7;
        }

        &::before {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(-5px, -50%);
          width: 1px;
          height: 30px;
          background-color: #000;

          @media (min-width: 1200px) {
            height: 40px;
            transform: translate(-20px,-50%);
          }
        }
      }
    }

    &-body {
      padding: 0 !important;

      .label {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0px;
        left: -1px;
        padding-bottom: 2px;
        width: 56px;
        height: 25px;
        background-color: #000;
        font-family: "GothamPro-Bold", sans-serif;
        font-style: normal;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 13px;
        color: #FFFFFF;
        z-index: 12;
        border-radius: 0;
        text-decoration: none;

        @media (min-width: 1200px) {
          width: 61px;
          height: 30px;
          font-size: 15px;
          left: 0;
        }
      }

      .pic {
        display: block;
        position: relative;
        width: 84.68%;
        height: auto;
        margin: 0 auto 15px;
        text-decoration: none;

        @media (min-width: 1200px) {
          width: 324px;
          margin: 0;
        }

        &::before {
          content: '';
          display: block;
          position: relative;
          width: 100%;
          height: auto;
          padding-bottom: 100%;
        }

        img {
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-size: cover;
          border: none;
          z-index: 1;

          &::before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #f3f3f3;
            background-position: center;
            background-repeat: no-repeat;
            background-size: 40%;
            background-image: url("data:image/svg+xml;base64,");
            z-index: 1;
          }
        }
      }
    }

    &-footer {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 0 !important;
      text-align: left;
      border-top: none;

      @media (min-width: 1200px) {
          width: 100%;
      }

      .title {
        display: block;
        width: 100%;
        height: auto;
        margin: 0 0 16px 0;
        padding: 0;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        font-family: "GothamPro-Medium", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        color: #000;
        text-decoration: none;
      }

      .code {
        display: block;
        width: 100%;
        height: auto;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        text-decoration: underline;
        text-decoration-color: rgba(0,0,0, 0.3);
        color: #000;
        margin: 0 0 19px 0;
        background-color: transparent;
      }

      .sale {
        display: block;
        position: relative;
        font-weight: 400;
        font-size: 12px;
        color: #999;
        margin-bottom: 7px;
        text-decoration: line-through;
        text-decoration-color: rgba(153, 153, 153, .6);
      }

      .price {
        font-family: "GothamPro-Bold", sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        color: #000;
        margin-bottom: 17px;
        text-decoration: none;

        @media (min-width: 1200px) {
          margin-bottom: 20px;
          min-height: 55px;
        }
      }
    }

    &-buttons {
      display: flex;
      align-items: center;
      gap: 20px;
      width: 100%;

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 40px;
        padding: 0 !important;
        margin: 0 !important;
        font-family: "GothamPro-Medium", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        text-transform: uppercase;

        @media (min-width: 1200px) {
            width: auto;
            padding: 0 33px!important;
        }

        &.btn_white {
          &::after {
            content: 'покупки';
            display: none;

            @media (min-width: 1200px) {
                display: inline-block;
                margin-left: 4px;
            }
          }
        }

        &.btn_black {
          &::after {
            content: 'заказ';
            display: none;

            @media (min-width: 1200px) {
                display: inline-block;
                margin-left: 4px;
            }
          }
        }
      }
    }

    &-delivery {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      width: 100%;
      height: auto;
      padding: 6px 0 0 0;
      margin-top: 9px;

      @media (min-width: 1200px) {
          padding: 0;
          margin-top: 20px;
      }

      i {
        flex: 1 0 auto;
        display: block;
        position: relative;
        width: 40px;
        max-width: 40px;
        height: 40px;
        border: 1px solid #000;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml;base64,");
      }

      span {
        flex: 1 0 auto;
        display: block;
        position: relative;
        width: 1px;
        max-width: 1px;
        height: 40px;
        background-color: #000;
        margin: 0 !important;
      }

      p {
        display: block;
        margin: 0;
        padding: 0;
        width: 100%;
        height: auto;
        font-family: "GothamPro", sans-serif !important;
        font-style: normal !important;
        font-weight: 400 !important;
        font-size: 12px !important;
        line-height: 120% !important;
        margin-right: -15px;

        @media (min-width: 1200px) {
          margin: 0;
          min-height: initial;
          font-size: 14px !important;
          padding-bottom: 2px;
        }
      }
    }
  }
}
