#catalog-nav {
    @media (min-width: 1201px) {
        display: block;
        columns: 3;
    }

    .unit {
        @media (min-width: 1201px) {
            break-inside: avoid;
            width: 100%;
            margin-bottom: 15px;
        }

        .tit {
            display: flex;
            gap: 15px;

            @media (min-width: 769px) {
                gap: 10px;
            }

            @media (min-width: 1201px) {
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                gap: 5px;
                padding: 10px 20px 0 0;
            }

            a {
                display: inline-block;
                font-size: 14px;
                line-height: 15px;
                text-decoration: underline;
                color: #999;

                @media (min-width: 1201px) {
                    font-size: 15px;
                }

                @media (hover: hover) and (pointer: fine) {
                    &:hover {
                        text-decoration: none;
                    }
                }
            }

            .tit_name {
                display: -webkit-box !important;
                max-width: calc(100% - 45px);
                overflow: hidden;
                margin-left: 0;
                padding-left: 45px;
                font-family: GothamPro-Bold;
                text-transform: uppercase;
                text-decoration: none;
                color: #000;
                -webkit-box-orient: vertical;
                text-overflow: ellipsis;
                -webkit-line-clamp: 1;

                @media (min-width: 769px) {
                    padding-left: 0;
                }

                @media (min-width: 1200px) {
                    display: block !important;
                    line-height: 1.33;
                }
            }
        }

        .submenu {
            display: none;
            padding: 5px 0 0 40px;
            margin-bottom: 0;

            @media (min-width: 1201px) {
                display: block;
                padding: 10px 0;
            }

            a {
                display: inline-flex;
                align-items: center;
                column-gap: 20px;
                position: relative;
                padding: 0 10px 0 15px;
                margin: 0;
                font-size: 15px;
                line-height: 25px;
                text-decoration: none;
                color: #000;

                @media (hover: hover) and (pointer: fine) {
                    &:hover::before {
                        background: red;
                    }
                }

                &::before {
                    content: "";
                    z-index: 1;
                    position: absolute;
                    left: 0;
                    top: 11px;
                    width: 4px;
                    height: 4px;
                    border-radius: 50%;
                    background: #000;
                    transition: .2s;
                }
            }
        }
    }
}
