.project-list {

    margin-bottom: 40px;

    @media (min-width: 1200px) {
        margin-bottom: 60px;
    }

    &__wrapper {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 20px;

        @media (min-width: 1200px) {
            row-gap: 60px;
        }
    }

    &__item {
        display: block;
        position: relative;
        width: 100%;
        height: auto;

        @media (min-width: 768px) {
            width: calc((100% - 40px) / 3);

            &:nth-child(4n + 2) {
                i:has(img) {
                    aspect-ratio: 453 / 365;
                }
            }
        }

        &:nth-child(4n) {
            width: 100%;

            i:has(img) {
                aspect-ratio: 3/ 1;
            }
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        gap: 10px;
        text-decoration: none !important;

        @media (hover: hover) and (pointer: fine) {
            &:hover {
                & > p {
                    color: #999;
                }

                & > i img {
                    opacity: 0.9;
                }
            }
        }
    }

    &__pic {
        flex-shrink: 0;
        display: block;
        position: relative;
        width: 100%;
        height: auto;
        aspect-ratio: 1 / 1;
        overflow: hidden;

        img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
            transition: all .3s ease-out;

            &::before {
                content: '';
                display: block;
                position: absolute;
                z-index: 1;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: #f5f5f5 no-repeat center;
                background-size: auto 50%;
                background-image: url("data:image/svg+xml;base64,");
            }
        }
    }

    &__info {
        text-wrap: balance;
        display: flex;
        justify-content: space-between;
        gap: 20px;
        height: 100%;
        margin: 6px 0;
        font-family: 'GothamPro-Light', sans-serif;
        color: #000;
        font-size: 17px;
        font-style: normal;
        font-weight: 300;
        line-height: 1.07;

        span:first-of-type {
            flex-shrink: 0;
        }

        span:last-of-type {
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
            text-align: right;
        }
    }

    &__desc {
        flex-shrink: 0;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        transition: all .3s ease-out;

        margin: 6px 0;
        font-family: "GothamPro-Medium", sans-serif;
        color: #000;
        font-size: 17px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.07;
    }
}