.main_slider .swiper-slide {
    height: auto;
}

.main_slider .swiper-slide .main_slider__video-wrap {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
}

.main_slider .swiper-slide .main_slider__video-wrap::before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80px;
    height: 80px;
    z-index: 2;
    background-color: transparent;
    background-image: url("data:image/svg+xml;base64, ");
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
}

@media (min-width: 769px) {
    .main_slider .swiper-slide .main_slider__video-wrap::before {
        width: 90px;
        height: 90px;
    }
}

@media (min-width: 993px) {
    .main_slider .swiper-slide .main_slider__video-wrap::before {
        width: 100px;
        height: 100px;
    }
}

.main_slider .swiper-slide .main_slider__video-wrap.playing::before {
    display: none;
}

.main_slider .swiper-slide .main_slider__video-wrap .media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
    margin: 0;
}

.main_slider .swiper-slide .main_slider__video-wrap .media_mob {
    display: block;
}

.main_slider .swiper-slide .main_slider__video-wrap .media_tab {
    display: none;
}

.main_slider .swiper-slide .main_slider__video-wrap .media_desc {
    display: none;
}

@media (min-width: 769px) {
    .main_slider .swiper-slide .main_slider__video-wrap .media_mob {
        display: none;
    }

    .main_slider .swiper-slide .main_slider__video-wrap .media_tab {
        display: block;
    }
}

@media (min-width: 993px) {
    .main_slider .swiper-slide .main_slider__video-wrap .media_tab {
        display: none;
    }

    .main_slider .swiper-slide .main_slider__video-wrap .media_desc {
        display: block;
    }
}

.main_slider_content-video-smart {
    top: initial !important;
    bottom: 70px;
}

@media (min-width: 577px) {
    .main_slider_content-video-smart {
        bottom: 100px;
    }
}

@media (min-width: 769px) {
    .main_slider_content-video-smart {
        bottom: 150px;
    }
}

@media (min-width: 993px) {
    .main_slider_content-video-smart {
        bottom: 130px;
    }
}

.main_slider_content-video-smart .main_slider_button {
    padding-top: 0;
    margin-top: 0;
}

.main_slider_content-video-smart .main_slider_button a {
    margin: 0 !important;
    left: 20%;
}

@media (min-width: 769px) {
    .main_slider_content-video-smart .main_slider_button a {
        left: 18%;
    }
}

@media (min-width: 993px) {
    .main_slider_content-video-smart .main_slider_button a {
        left: 10%;
    }
}