.log-conf {

  color: black;

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    height: auto;
    background-color: white;
    border: 1px solid black;
    margin: 30px 0;
    padding: 117px 9px 126px;

    @media (min-width: 768px) {
        margin: 50px 0;
        padding-bottom: 108px;
    }
  }

  &__title {
    font-family: "GothamPro-Medium", sans-serif;
    font-size: 20px;
    line-height: 19px;
    text-align: center;
    margin-bottom: 30px;

    @media (min-width: 768px) {
      font-size: 38px;
      line-height: 36px;
      margin-bottom: 31px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    width: 100%;
    height: auto;
    margin-top: 30px;

    @media (min-width: 768px) {
        margin-top: 32px;
    }

    &.error {
      .log-conf__form-err {
        display: block;
      }
    }
    
    &-controller {
      display: flex;
      flex-direction: column;
      gap: 5px;
      width: 100%;
      max-width: 300px;
      margin-bottom: 10px;
    }

    &-input {
      display: flex;
      align-items: center;
      padding: 16px 8px 17px;
      border: 1px solid #b1b1b1;
      border-radius: 3px;
      font-size: 13px;

      &::-webkit-input-placeholder {
          color: #b1b1b1;
          opacity: 1;
          transition: opacity 0.2s ease;
      }

      &::-moz-placeholder {
          color: #b1b1b1;
          opacity: 1;
          transition: opacity 0.2s ease;
      }

      &:-moz-placeholder {
          color: #b1b1b1;
          opacity: 1;
          transition: opacity 0.2s ease;
      }

      &:-ms-input-placeholder {
          color: #b1b1b1;
          opacity: 1;
          transition: opacity 0.2s ease;
      }

      &:focus::-webkit-input-placeholder {
          color: #b1b1b1;
          opacity: 0;
          transition: opacity 0.2s ease;
      }

      &:focus::-moz-placeholder {
          color: #b1b1b1;
          opacity: 0;
          transition: opacity 0.2s ease;
      }

      &:focus:-moz-placeholder {
          color: #b1b1b1;
          opacity: 0;
          transition: opacity 0.2s ease;
      }

      &:focus:-ms-input-placeholder {
          color: #b1b1b1;
          opacity: 0;
          transition: opacity 0.2s ease;
      }
    }

    &-err {
      display: none;
      position: relative;
      top: -30px;
      width: 100%;
      height: 0;      
      text-align: center;

      @media (min-width: 768px) {
        top: -32px;
      }

      span {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 100%;
        height: auto;
        font-size: 15px;
        line-height: 130%;
        color: red;

        @media (min-width: 768px) {
          font-size: 16px;
        }
      }
    }

    &-btn {
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 185px;
      height: 40px;
      margin-top: 14px;
      background: black;
      border: 1px solid black;
      font-family: "GothamPro-Medium", sans-serif;
      color: white;
      font-size: 12px;
      text-transform: uppercase;
      transition: all .1s linear;

      @media (min-width: 1200px) {
          &:hover {
            background-color: white;
            color: black;
          }
      }
    }
  }
}
